        methods: {
        lb_left(){
            this.num++
            if(this.num>=1){
                this.num=-3
            }
        },
        lb_right(){
            this.num--
            if(this.num<=-4){
                this.num=0
            }
        }
    },
    mounted () {
        setInterval(() => {
            this.num--
